  line-height: 1.5;
  --base-color: rgba(25, 25, 25, 0.99);
  --bg-light-color: rgba(35, 35, 35, 0.975);
  --bg-lighter-color: rgba(38, 38, 38, 0.975);
  --bg-alt-color: rgba(59, 59, 72, 0.619);
  --highlight-color: rgba(98, 97, 97, 0.395);
}

h1,
h2,
h3,
h4,
h5,
figure,
p,
ol,
ul {
  margin: 0;
}

ol[role="list"],
ul[role="list"] {
  list-style: none;
  padding-inline: 0;
}

h1,
h2,
h3,
h4,
h5 {
  font-size: inherit;
  font-weight: inherit;
}

img {
  display: block;
  max-inline-size: 100%;
}

body {
  color: white;
  background-color: var(--base-color);
  font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  display: flex;
}

.icon-and-text {
  display: flex;
  gap: 15px;
  align-items: center;
}

.icon,
.circle-mark,
.star-mark {
  filter: invert(100%) brightness(1000%);
  width: 30px;
}

.left-sidebar,
.main,
.right-sidebar {
  display: flex;
  flex-direction: column;
  font-size: 1.2rem;
}

.left-sidebar {
  padding: 15px 10px;
  flex: 2;
  background-color: var(--bg-light-color);
  gap: 20px;
  border-radius: 0 15px 15px 0;
  box-shadow: 8px 0 10px -4px rgba(0, 0, 0, 0.3);
}

.page-title {
  margin-left: 15px;
}

.page-title > .logo {
  width: 40px;
}

.title-text {
  font-size: 2rem;
  font-weight: 500;
}

#main-list,
#project-list {
  display: flex;
  flex-direction: column;
  gap: 2px;
}

#main-list > div,
#project-list > p {
  gap: 0;
  cursor: pointer;
  padding: 4px 15px;
}

#main-list > div:hover,
#project-list > p:hover {
  background-color: rgba(30, 30, 30, 0.975);
}

#main-list > div:active,
#project-list > p:active {
  background-color: rgba(25, 25, 25, 0.975);
}

#main-list > div > p {
  padding-left: 15px;
  flex: 1;
}

.creation-section {
  display: flex;
  align-items: baseline;
}

.creation-section > label {
  font-size: 2.2rem;
  line-height: 1;
  cursor: pointer;
}

input {
  background-color: inherit;
  border: none;
  color: white;
  line-height: 1.5;
  padding: 0.3rem;
  border-style: none;
  border-radius: 5px;
  border-bottom: 2px solid white;
}

input:focus {
  outline: none;
  border-bottom: 3px solid rgb(54, 108, 225);
}

.main {
  padding: 40px 60px;
  min-height: 100vh;
  flex: 6;
  gap: 15px;
}

.project-section {
  display: flex;
  justify-content: space-between;
  font-size: 2rem;
}

.project-section > img {
  filter: invert(100%) brightness(1000%);
  width: 35px;
}

#project-deletion {
  cursor: pointer;
}

.no-display {
  display: none;
}

#task-list,
#completed-list,
.main > div:last-child {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

#task-list > div,
#completed-list > div,
#task {
  display: flex;
}

#task-list,
#completed-list {
  gap: 3px;
}

#task-list > div,
#completed-list > div {
  padding: 0.4rem;
  border-style: none;
  border-radius: 5px;
  background-color: var(--bg-lighter-color);
  cursor: pointer;
}

#task-list > div:hover,
#completed-list > div:hover {
  background-color: var(--bg-light-color);
}

#task-list > div:active,
#completed-list > div:active {
  background-color: rgba(40, 40, 40, 0.975);
}

#task-list > div > p,
#completed-list > div > p {
  padding: 0 15px;
  flex: 1;
}

#completed-list > div > p {
  text-decoration: line-through;
}

#completed-list-title > * {
  cursor: pointer;
}

#task {
  gap: 15px;
}

.star-mark, .circle-mark {
  cursor: pointer;
}

.star-mark {
  margin-left: auto;
}

.right-sidebar {
  padding: 40px 40px;
  gap: 30px;
  flex: 3;
  background-color: var(--bg-lighter-color);
  border-radius: 15px 0 0 15px;
  box-shadow: -8px 0 10px -4px rgba(0, 0, 0, 0.3);
}

label[for="due-date"] {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 5px;
}

label[for="due-date"] > div {
  cursor: pointer;
}

#due-date, textarea {
  color: black;
  resize: none;
  border-radius: 8px;
  padding: 7px 10px;
  background-color: rgba(210, 210, 210, 0.95);
}

#due-date {
  padding: 5px 7px;
  cursor: text;
}

#due-date::-webkit-calendar-picker-indicator {
  cursor: pointer;
}

#task-buttons {
  display: flex;
  justify-content: space-between;
}

#task-buttons > button {
  color: white;
  background-color: var(--base-color);
  padding: 7px 14px;
  border: none;
  border-radius: 8px;
  font-size: 1.2rem;
  font-weight: 600;
  cursor: pointer;
  box-shadow:
    inset 0 -1px 2px rgba(0, 0, 0, 0.5),
    inset 0 1px 2px rgba(255, 255, 255, 0.2);
}

#task-deletion:hover {
  background-color: rgb(188, 13, 13);
}

#detail-confirmation:hover {
  background-color: rgb(54, 108, 225);
}

.hidden {
  visibility: hidden;
}
